.keyboard {
    margin: auto;
    float: center;
    width: fit-content;
    
}

.keyboard-row {
    display: flex;
    flex-direction: row;
}
.keyboard-row:nth-of-type(1n) {
    margin-left: 0;
    margin-right: 0;
}
.keyboard-row:nth-of-type(2n) {
    margin-left: 1rem;
    margin-right: 1rem;
}
.keyboard-row:nth-of-type(3n) {
    margin-left: 0;
    margin-right: 0;
}